import React, { useState, useEffect } from "react";
import Styled from "styled-components";
import Pdf from "../Images/pdf.png";
import axios from "axios";
import { Link } from "react-router-dom";

function Masters() {
  const [response, setresponse] = useState([]);
  const API = process.env.REACT_APP_API;

  useEffect(() => {
    window.scroll(0, 0);
    axios
      .get(`${API}/course/mbe/view/`)
      .then((res) => {
        console.log(res.data.data);
        setresponse(res.data.data);
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);
  return (
    <div>
      <Container>
        <Box>
          <h1>Masters in Bioethics Program (MBE)</h1>
          <Bottom>
            <div className="innerbox">
              <p className="masters">
                Masters in Bioethics is offered as a two year regular course
                consisting four semesters. Admission to the course will be based
                on the performance of the candidate at the Common Admission Test
                (CAT), Group Discussion and Interview.
              </p>

              <Div>
                <p className="number">Who can apply?</p>

               
              </Div>
              <p className="medium">
                  Candidates for admission to the course must have passed M.B.B.
                  S./ Bachelor’s degree in Law/ B.D.S./ B. Sc in any of the life
                  sciences/ B. Sc Agriculture/ B. Pharm/ B. H. A. (Bachelor of
                  Hospital Administration)/ B. V. Sc (Bachelor of Veterinary
                  Science).
                </p>
              <p class="disclaimer">
                ** Subject to changes made to these regulations from time to
                time by the University.
              </p>
              {response.map((res)=>(
            <a href={res.file} target="_blank">

              <img src={Pdf} alt="pdf" />
            <h3>{res.file_name}</h3>

            </a>

            ))}
              <Link to="/phdarchive">
                <p className="end">Click here to view archived files</p>
              </Link>
            </div>
          </Bottom>
        </Box>
      </Container>
    </div>
  );
}

export default Masters;

