import axios from "axios";
import "slick-carousel/slick/slick.css";
import Slider from "react-slick";
import "./Style.css";

export default function Spotlight() {
  const settings = {  
    dots: false,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    autoplayTimeOut: 800,
    arrows: true,
  };
  const [responses, setResponses] = useState([]);
  const [response, setresponse] = useState([]);
  const API = process.env.REACT_APP_API;


  useEffect(() => {
    axios
      .get(`${API}/news/titleview/`)
      .then((res) => {
        console.log(res.data.data, "first");

        setresponse(res.data.data);
      })
      axios
      .get(`${API}/home/view/`)
      .then((res) => {
        console.log(res.data.data, "second");

        setResponses(res.data.data);
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);
  return (
    <OVERLAY>
      <Image>
        <Slider {...settings}>
        {responses.map((res) => (
          
            <CarouselImage>
              <img src={res.image} />
                <div class="carousel">
                  <h1>{res.quote}</h1>
                </div>
           
            </CarouselImage>
          
          ))}
          </Slider>
      </Image>
      <DIV>
        <BOX>ICREP</BOX>
        <Marquee>
          {response.map((res) => (
            <a href="/news">
              <Span>*</Span>
              {res.title}
            </a>
          ))}
        </Marquee>
      </DIV>
    </OVERLAY>
  );
}

