import React, { useState, useEffect } from 'react';
import Styled from "styled-components";
import Pdf from "../Images/pdf.png";
import { Link } from "react-router-dom";
import axios from "axios";

export default function ThreeyearllbArchive() {
    const [response, setresponse] = useState([]);
    const API = process.env.REACT_APP_API;
    useEffect(() => {
      axios
        .get(`${API}/course/three_year_llb/archive/`)
        .then((res) => {
          console.log(res.data.data,);
          setresponse(res.data.data);
        })
        .catch((err) => {
          console.log(err);
        });
    }, []);
    return (
        <Box>
            <Container>
                <h1>Archive</h1>
                <Flex>
                {response.map((res) => (
                    <Card><a href={res.file}>
                        <img src={Pdf} alt="pdf" />
                        <p>{res.file_name}</p></a>
                    </Card>
                ))}
                </Flex>
            </Container>
        </Box>
    )
}
const Box = Styled.div`
    max-width:1280px;
    margin :50px auto;
    h1{
        font-family: Oswald;
        font-style: normal;
        font-weight: normal;
        margin:0px;
        padding-bottom:0px;
        font-size: 32px;
        color: #E01E38;
        margin-bottom:50px;
        margin-top:36px;
    }
`;
const Card = Styled.div`
    display:flex;
    flex-direction:column;
    width:250px;
    img{
        width:100px;
        height:125px;
        margin:0 auto;
    }
    p{
        width:80%;
        margin:10px auto;
        text-align:center;
        color:#000;
    }
    a{
        text-align: center;
        text-decoration: none;
    }
`;
const Flex = Styled.div`
    display:flex;
    flex-wrap:wrap;
    justify-content:space-between;
`;
const Container = Styled.div`
    width:80%;
    margin:0 auto;
`;

