import React,{ useState, useEffect }  from 'react'
import Styled from 'styled-components';
import Pdf from "../Images/pdf.png";
import axios from "axios";
import { Link } from "react-router-dom";

function BCom() {

    const [response, setresponse] = useState([]);
    const API = process.env.REACT_APP_API;
  
    useEffect(() => {
        window.scroll(0,0);
      axios
        .get(`${API}/course/bcom_llb/view/`)
        .then((res) => {
          console.log(res.data.data);
          setresponse(res.data.data);
        })
        .catch((err) => {
          console.log(err);
        });
    }, []);
    return (
        <div>
        <Container>
            
            <Box>
                <h1>B. Com. LLB (Hons.)
</h1>
<Bottom>
                <p className="top">A trader, or an Accountant cannot survive without knowing the Laws which govern him.
                     For a good Lawyer, the knowledge of nuances of commercial activities will be an added advantage.
                      This dual degree course consists of commerce related subjects such as Financial Accounting,
                       Corporate Accounting, Entrepreneurship etc., and the core Law subjects. This gives the students an
                        immense opportunity to have the knowledge of Law with a Commerce background, or the knowledge of
                         Commerce, with a Law background. During the last two semesters the course is specialized on
                          different branches of a single subject like Corporate Governance through special electives. 
                          This gives the students an opportunity to specialize in this subject as in a Post Graduate 
                          programme as the papers are of Post-Graduation standard. This gives the students a deep knowledge 
                          about the legal aspects of Commerce, and the Commercial aspect of Law. This course thus gives
                           the double benefit of having graduated two subjects.     </p>
                    <Div>
                        <p className="number">Numbers of Seats</p>
                        <p className="colon">:</p>
                        <p className="medium">30 + Supernumerary</p>
                    </Div>
                    <Div>
                        <p className="number">Duration</p>
                        <p className="colon">:</p>
                        <p className="medium">5 years (Ten Semesters) </p>
                    </Div>
                    <Div>
                        <p className="number">Instruction Medium</p>
                        <p className="colon">:</p>
                        <p className="medium">English</p>
                    </Div>
                    <Div>
                        <p className="number">Curriculum</p>
                        <p className="colon">:</p>
                        <p className="medium">The subjects for the course shall be prescribed by the Cochin University of Science and Technology from time to time in accordance with the rules of the Bar Council of India.
 </p>
                    </Div>
                    <H6>Minimum Qualifications Required for Applying:</H6>
                    <p className="end">At the time of admission the candidate should have passed plus two examination with at least 60% marks including languages in Science group/Commerce group or with at least 55% marks including languages in Arts/Humanities stream.

Candidates belonging to SC/ST communities or belonging to SEBC communities with a pass in the plus two examination with 50% percent marks are entitled to seek admission.


 </p>
                    <Div>
                        <p className="number">Admission Procedure</p>
                        <p className="colon">:</p>
                        <p className="medium">Admission through Common Entrance Test (CUSAT - CAT) **</p>
                    </Div>
                    <p className="disclaimer">** Subject to changes made to these regulations from time to time by the University.</p>
                    <a href={response.file} target ="_blank"><img src={Pdf} alt="pdf" />Read more</a>
                    <Link to="/phdarchive"><p className="end">
              Click here to view archived files
            </p></Link>
                    </Bottom>
            </Box>
        </Container>
        </div>
    )
}

export default BCom

